@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@600&display=swap);
*, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  background-color: #FF9D0A;
  color: #3C096C;
  font-family: 'Poppins', 'Oxygen', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.letter {
    max-height: 100vh;
    display: none;
    overflow-y: hidden;
    overflow-x: scroll;
    -ms-scroll-snap-type: x mandatory;
        scroll-snap-type: x mandatory;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.letter::-webkit-scrollbar {
    display: none;
  }

.letter-section {
    position: relative;
    display: flex;
    align-items: center;
    height: 100vh;
    min-width: 100vw;
    overflow: hidden;
    scroll-snap-align: start;
    scroll-snap-stop: always;
    scroll-margin: 0;
    scroll-padding: 0;
}

.s2 {
    justify-content: center;
}

.s3 {
    justify-content: flex-end;
}

.bubble-text {
    position: relative;
    isolation: isolate;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 6rem;
    width: 60vw;
    aspect-ratio: 1/1;
    border-radius: 50%;
}

.bubble-text::after {
    content: "";
    position: absolute;
    z-index: -1;
    inset: 0;
    border-radius: 50%;
    background-color: #3C096C;
    -webkit-animation: float 4s ease 1s infinite;
            animation: float 4s ease 1s infinite;
}

.p {
    color: #FFDEAD;
    text-align: justify;
}

.scroll {
    align-self: flex-end;
    margin: 0 0 5rem 2rem;
    font-family: "Gluten";
    font-size: 2rem;
}

#companyLogo {
    position: absolute;
    z-index: -1;
    right: 0;
    top: 50%;
    margin-top: -4rem;
    height: 8rem;
    -webkit-animation: logo 2s ease alternate infinite;
            animation: logo 2s ease alternate infinite;
}

#skills i{
    display: block;
    font-size: 2rem;
    padding: 0.5rem 0;
    margin-right: 5vw;
    -webkit-animation: skills 1.6s ease alternate infinite;
            animation: skills 1.6s ease alternate infinite;
}

#contact {
    position: absolute;
    z-index: 2;
    bottom: 5vh;
    right: 30vw;
    margin-right: -4rem;
    -webkit-animation: contact 1s ease-in-out alternate infinite;
            animation: contact 1s ease-in-out alternate infinite;  
}

#contact a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: #FF9D0A;
    font-size: 1.5rem;
    margin: 0 0.5rem;
    color: #3C096C;
    text-decoration: none;
}

@-webkit-keyframes float {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(3%);
                transform: translateY(3%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

}

@keyframes float {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(3%);
                transform: translateY(3%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

}

@-webkit-keyframes logo {
    0% {
        -webkit-transform: translateX(0) rotate(0) scale(50%);
        transform: translateX(0) rotate(0) scale(50%);
    }
    60% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(100%);
        transform: translateX(90%) rotate(720deg) scale(100%);
    }
    65% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(105%);
        transform: translateX(90%) rotate(720deg) scale(105%);
    }
    70% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(95%);
        transform: translateX(90%) rotate(720deg) scale(95%);
    }
    75% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(105%);
        transform: translateX(90%) rotate(720deg) scale(105%);
    }
    100% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(100%);
        transform: translateX(90%) rotate(720deg) scale(100%);
    }
}

@keyframes logo {
    0% {
        -webkit-transform: translateX(0) rotate(0) scale(50%);
        transform: translateX(0) rotate(0) scale(50%);
    }
    60% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(100%);
        transform: translateX(90%) rotate(720deg) scale(100%);
    }
    65% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(105%);
        transform: translateX(90%) rotate(720deg) scale(105%);
    }
    70% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(95%);
        transform: translateX(90%) rotate(720deg) scale(95%);
    }
    75% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(105%);
        transform: translateX(90%) rotate(720deg) scale(105%);
    }
    100% {
        -webkit-transform: translateX(90%) rotate(720deg) scale(100%);
        transform: translateX(90%) rotate(720deg) scale(100%);
    }
}

@-webkit-keyframes skills {
    0% {
        -webkit-transform: scaleX(0%);
        transform: scaleX(0%);
    }
    30% {
        -webkit-transform: scaleX(100%);
        transform: scaleX(100%);
    }
    100% {
        -webkit-transform: scaleX(100%);
        transform: scaleX(100%);
    }
}

@keyframes skills {
    0% {
        -webkit-transform: scaleX(0%);
        transform: scaleX(0%);
    }
    30% {
        -webkit-transform: scaleX(100%);
        transform: scaleX(100%);
    }
    100% {
        -webkit-transform: scaleX(100%);
        transform: scaleX(100%);
    }
}

@-webkit-keyframes contact {
    0% {
        -webkit-transform: rotate(5deg);
        transform: rotate(5deg);
    }
    100% {
        -webkit-transform: rotate(-5deg);
        transform: rotate(-5deg);
    }
}

@keyframes contact {
    0% {
        -webkit-transform: rotate(5deg);
        transform: rotate(5deg);
    }
    100% {
        -webkit-transform: rotate(-5deg);
        transform: rotate(-5deg);
    }
}

@media screen and (max-width: 1000px) {
    .letter {
        max-height: none;
        display: none;
        overflow-y: visible;
        overflow-y: initial;
        flex-direction: column;
    }
    .letter-section {
        overflow: visible;
        overflow: initial;
    }
    .bubble-text {
        width: auto;
        width: initial;
        border-radius: 0%;
        padding: 2rem;
    }
    .bubble-text::after {
        display: none;
    }
    .p {
        color: #3C096C;
    }
    .scroll, #companyLogo, #skills {
        display: none;
    }
}
.login {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

input, button {
  border: none;
  background: none;
  color: #3C096C;
}

input:focus{
  outline: none;
}

#password {
  padding: 1rem;
  border-radius: 25px;
  border-top-left-radius: 18px;
  border-bottom-right-radius: 18px;
  border-left: 2px #3C096C solid;
  border-bottom: 2px #3C096C solid;
  font-size: 1.5rem;
}

button {
  height: 8rem;
  border-radius: 50%;
  margin-left: -2rem;
  margin-top: -1.5rem;
  cursor: pointer;
}

.login img {
  height: 8rem;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 1px 1px 4px grey;
}

@media screen and (max-width: 1000px) {
  input {
      max-width: 80vw;
  }
  button {
      height: 5rem;
      margin-top: 0rem;
  }
  .login img {
      height: 5rem;
  }
}
